//@import-normalize
@import "helper.scss";
*{
  margin: 0;
  padding:0;
}
ul,ol{
  list-style: none;
}
*{
  box-sizing: border-box;
}
*::before,*::after{
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: inherit;
}

body{
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
}

button,input{
  font:inherit;
}

svg{
  width: 1em;
  height:1em;
}
:focus{
  outline:none;
}


button{
  font-family: inherit;
}

